import { NextRequest, NextResponse } from 'next/server';
import { GoogleGenerativeAI } from '@google/generative-ai';

const GEMINI_API_KEY = process.env.GEMINI_API_KEY || 'AIzaSyDjnsgbo5yc2iroteGTz5qVe0DvruZxS2M';
const genAI = new GoogleGenerativeAI(GEMINI_API_KEY);

export async function GET(request: NextRequest) {
  try {
    // List of common Gemini model names to test
    const modelsToTest = [
      'gemini-pro',
      'gemini-1.5-flash',
      'gemini-1.5-pro',
      'gemini-1.5-flash-latest',
      'gemini-pro-vision',
      'gemini-1.0-pro-vision-latest',
    ];

    const results: Record<string, boolean> = {};
    const errors: Record<string, string> = {};

    // Test each model
    for (const modelName of modelsToTest) {
      try {
        const model = genAI.getGenerativeModel({ model: modelName });
        // Try to generate content to see if model works
        const result = await model.generateContent('Test');
        await result.response;
        results[modelName] = true;
      } catch (error: any) {
        results[modelName] = false;
        errors[modelName] = error.message || 'Unknown error';
      }
    }

    const availableModels = Object.keys(results).filter(m => results[m] === true);
    
    return NextResponse.json({
      availableModels,
      allResults: results,
      errors,
      recommendation: availableModels.length > 0 
        ? `Use model: ${availableModels[0]}` 
        : 'No working models found. Please check your API key and access level.',
    });
  } catch (error: any) {
    return NextResponse.json(
      { error: 'Failed to test models', details: error.message },
      { status: 500 }
    );
  }
}

